.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
  margin-bottom: 5rem;
}
@media (min-width: 1024px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100%;
}
@media (min-width: 1024px) {
  .navegacion {
    flex-direction: row;
    width: auto;
  }
}
.navegacion__link {
  font-size: 2rem;
  font-weight: 700;
  color: #000;
}
.navegacion__link--registrar {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.navegacion__link--registrar:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .navegacion__link--registrar {
    width: auto;
  }
}

@media (min-width: 768px) {
  .formulario__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.formulario__contenido {
  background-color: #e8505b;
  padding: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.formulario__heading {
  color: #FFFFFF;
  text-align: center;
}
.formulario__campo {
  margin-bottom: 1rem;
}
.formulario__label {
  display: block;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 1rem 0;
}
.formulario__input {
  width: 100%;
  padding: 1.5rem;
  border: none;
}
.formulario__submit {
  margin-top: 2rem;
  background-color: #455a64;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.formulario__submit:hover {
  cursor: pointer;
  background-color: #303f46;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
@media (min-width: 768px) {
  .formulario__submit {
    width: 100%;
  }
}
.formulario__imagen-contenedor {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
}
.formulario__imagen {
  width: 30rem;
}
@media (min-width: 1024px) {
  .formulario__imagen {
    width: 100%;
  }
}

.pasos {
  margin-top: 5rem;
}
.pasos__wave {
  margin-bottom: -1px;
}
.pasos__contenido {
  background-color: #e8505b;
}
.pasos__heading {
  text-align: center;
  color: #FFFFFF;
}
.pasos__heading--descripcion {
  font-size: 1.8rem;
  display: block;
}
@media (min-width: 768px) {
  .pasos__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.pasos__imagen {
  transition-property: transform;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
}
.pasos__imagen:hover {
  transform: rotate(-3deg) scale(1.1);
}

.testimonial {
  background-color: #455a64;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  color: #FFFFFF;
}
.testimonial:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .testimonial {
    margin-bottom: 0;
  }
  .testimonial:nth-child(1) {
    grid-column: 1/4;
  }
  .testimonial:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .testimonial:nth-child(4) {
    grid-column: 1/3;
  }
  .testimonial:nth-child(5) {
    grid-column: 3/5;
  }
}
.testimonial__header {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.testimonial__imagen {
  flex-basis: 10rem;
  overflow: hidden;
  border-radius: 50%;
}
.testimonial__autor {
  color: #FFFFFF;
  font-weight: 700;
  margin: 0.5rem 0;
}
.testimonial__texto {
  font-size: 2rem;
  line-height: 1.5;
  margin: 2rem 0;
  padding-left: 3rem;
  position: relative;
}
.testimonial__texto::before {
  content: '"';
  font-size: 10rem;
  position: absolute;
  left: 0;
  top: -4rem;
  opacity: 0.2;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}

.testimoniales {
  padding: 5rem;
}
.testimoniales__heading {
  text-align: center;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

.favorito {
  background-color: #FFFFFF;
  padding: 4rem;
  margin-bottom: 2rem;
  box-shadow: 0px 0px 2.6rem -0.8rem #99a4a9;
  border-radius: 1rem;
}
.favorito:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .favorito {
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .favorito__grid {
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 2rem;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .favorito__grid {
    grid-template-columns: 1fr 3fr;
    gap: 4rem;
  }
}
.favorito__imagen {
  max-width: 20rem;
  margin: 0 auto 2rem;
}
.favorito__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .favorito__contenido {
    align-items: flex-start;
  }
}
.favorito__nombre {
  margin: 1rem 0;
  color: #e8505b;
  font-size: 2.4rem;
}
.favorito__descripcion {
  margin: 0.5rem 0;
  text-align: center;
  font-size: 1.6rem;
  color: #455a64;
  line-height: 1.7;
}
@media (min-width: 768px) {
  .favorito__descripcion {
    text-align: left;
  }
}

.favoritos {
  background-color: #eceeef;
  padding: 5rem 0;
}
.favoritos__heading {
  text-align: center;
  color: #455a64;
}
@media (min-width: 768px) {
  .favoritos__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.gana {
  padding: 5rem;
}
.gana__heading {
  text-align: center;
  color: #455a64;
}
@media (min-width: 768px) {
  .gana__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.gana__imagen {
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .gana__imagen {
    margin-bottom: 0;
  }
}
.gana__texto {
  color: #455a64;
  font-size: 1.8rem;
  line-height: 1.7;
  margin: 0 0 1rem 0;
}
.gana__boton {
  margin-top: 4rem;
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.gana__boton:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .gana__boton {
    width: auto;
  }
}
@media (min-width: 1024px) {
  .gana__boton {
    width: 40%;
  }
}

.descargar {
  background-color: #eceeef;
  padding: 5rem;
}
@media (min-width: 768px) {
  .descargar__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.descargar__imagen {
  width: 50%;
  margin: 0 auto;
  transition-property: transform;
  transition-duration: 300ms;
}
.descargar__imagen:hover {
  transform: rotate(2deg) scale(1.1);
}

.tiendas {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tiendas__heading, .tiendas__texto {
  text-align: center;
}
.tiendas__heading {
  color: #455a64;
  margin: 2rem 0 0 0;
}
.tiendas__texto {
  margin-bottom: 3rem;
}
.tiendas__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
.tiendas__enlace {
  transition-property: transform;
  transition-duration: 300ms;
}
.tiendas__enlace:hover {
  transform: scale(1.1);
}

.footer {
  background-color: #455a64;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 5rem;
  }
}
.footer__widget {
  margin-bottom: 5rem;
}
.footer__widget:last-of-type {
  margin-bottom: 0;
}
.footer__heading {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__heading {
    text-align: left;
  }
}
.footer__nav {
  display: flex;
  flex-direction: column;
}
.footer__nav, .footer__texto {
  margin-top: 2rem;
}
.footer__link, .footer__texto {
  font-size: 2rem;
  line-height: 1.8;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__link, .footer__texto {
    text-align: left;
  }
}
.footer__copyright {
  background-color: #e8505b;
  color: #FFFFFF;
  text-align: center;
  padding: 2rem;
  margin: 0;
}